/**
 * 日時文字列生成処理
 * @param date Dateオブジェクト
 * @param delimiter 年月日のデリミタ
 * @param appendHms 時:分を追加する
 * @returns 日時文字列
 */
export function formatDateString(date: Date, delimiter: string, appendHms: boolean = false): string {
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0'); // 月は0から始まるので +1
  const day = String(date.getDate()).padStart(2, '0');

  let dateString = `${year}${delimiter}${month}${delimiter}${day}`;

  if (appendHms) {
    dateString += ` ${formatHhMmString(date)}`;
  }
  return dateString;
}

/**
 * 時分文字列生成処理
 * @param date Dateオブジェクト
 * @returns 'hh:mm' 形式の文字列
 */
export function formatHhMmString(date: Date): string {
  const hours = String(date.getHours()).padStart(2, '0');
  const minutes = String(date.getMinutes()).padStart(2, '0');
  return `${hours}:${minutes}`;
}

/**
 * '分'を 'hh:mm:ss' 形式に変換する
 * @param minutes 分
 * @returns 'hh:mm:ss' 形式の文字列
 */
export function convertMinutesToHHMMSS(minutes: number): string {
  // 時間、分、秒を計算
  const hours = Math.floor(minutes / 60);
  const remainingMinutes = minutes % 60;
  const seconds = 0; // 秒は常に0とする

  // 各値を2桁の文字列に変換
  const formattedHours = String(hours).padStart(2, '0');
  const formattedMinutes = String(remainingMinutes).padStart(2, '0');
  const formattedSeconds = String(seconds).padStart(2, '0');

  // 'hh:mm:ss' 形式で返す
  return `${formattedHours}:${formattedMinutes}:${formattedSeconds}`;
}

/**
 * 時間帯リスト生成処理
 * @param start 開始時刻('hh:MM'形式)
 * @param end 終了時刻('hh:MM'形式)
 * @param interval 時刻間隔
 * @returns 時間帯リスト
 */
export function generateTimeSlots(start: string, end: string, interval: number) {
  const slots = [];
  const [startHour, startMinute] = start.split(":").map(Number);
  const [endHour, endMinute] = end.split(":").map(Number);

  let currentHour = startHour;
  let currentMinute = startMinute;

  while (
    currentHour < endHour ||
    (currentHour === endHour && currentMinute < endMinute)
  ) {
    const nextMinute = currentMinute + interval;
    let nextHour = currentHour;
    if (nextMinute >= 60) {
      nextHour += 1;
    }

    const slotStart = `${padTime(currentHour)}:${padTime(currentMinute)}`;
    slots.push(`${slotStart}`);

    currentHour = nextHour;
    currentMinute = nextMinute % 60;
  }
  return slots;
}

function padTime(value: number) {
  return value.toString().padStart(2, "0");
}
