import axios, { AxiosInstance, AxiosResponse } from 'axios';

/**
 * Axiosインスタンス設定
 */
const apiClient: AxiosInstance = axios.create({
  baseURL: import.meta.env.VITE_BACKEND_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
});

/**
 * リクエストインターセプター
 */
apiClient.interceptors.request.use(
  (config) => {
    // TODO : 何かあれば追加。
    return config;
  },
  (error) => Promise.reject(error)
);

/**
 * レスポンスインターセプター
 */
apiClient.interceptors.response.use(
  (response) => response,
  (error) => {
    // TODO : エラーハンドリングを共通化する。
    if (error.response?.status === 401) {
      // do something
    }
    return Promise.reject(error);
  }
);

/**
 * GET処理
 * @param url エンドポイントを指定する
 * @param params Request paramを指定する
 * @returns GET結果(データ)
 */
export const getRequest = async <T>(url: string, params: object = {}, withCredentials: boolean = true): Promise<T> => {
  try {
    const response: AxiosResponse<T> = await apiClient.get(url, { params , withCredentials});
    return response.data;
  } catch (error) {
    console.error(`GET ${url} failed:`, error);
    throw error;
  }
};

/**
 * POST処理
 * @param url エンドポイントを指定する
 * @param params Request paramを指定する
 * @returns POST結果(データ)
 */
export const postRequest = async <T, U>(url: string, data: U, withCredentials: boolean = true): Promise<T> => {
  try {
    const response: AxiosResponse<T> = await apiClient.post(url, data, { withCredentials });
    return response.data;
  } catch (error) {
    console.error(`POST ${url} failed:`, error);
    throw error;
  }
};

/**
 * PUT処理
 * @param url エンドポイントを指定する
 * @param params Request paramを指定する
 * @returns 結果(データ)
 */
export const putRequest = async <T, U>(url: string, data: U, withCredentials: boolean = true): Promise<T> => {
  try {
    const response: AxiosResponse<T> = await apiClient.put(url, data, { withCredentials });
    return response.data;
  } catch (error) {
    console.error(`PUT ${url} failed:`, error);
    throw error;
  }
};

/**
 * DELETE処理
 * @param url エンドポイントを指定する
 * @returns 結果(データ)
 */
export const deleteRequest = async <T>(url: string, withCredentials: boolean = true): Promise<T> => {
  try {
    const response: AxiosResponse<T> = await apiClient.delete(url, { withCredentials });
    return response.data;
  } catch (error) {
    console.error(`DELETE ${url} failed:`, error);
    throw error;
  }
};

export default apiClient;
